import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Action } from '@ngrx/store';
import { Observable, of, EMPTY } from 'rxjs';
import { map, mergeAll, catchError, mergeMap } from 'rxjs/operators';
import { UserActions } from '../actions/todo.action';
import { userActionTypes } from '../shared/enum/todo-action';
import { sharedServices } from '../shared/shared.services';

@Injectable()
export class UserEffect {
    constructor(
        private _Action$: Actions,
        private _api: sharedServices

    ) { }
    @Effect()
    loadUser$ = this._Action$.pipe(
        ofType('[USER] Load User'),
        mergeMap(()=> {
         return this._api.loadUser().pipe(
            map((data:any) => ({ type: '[USER] Load User Success', payload: data })),
            catchError(() => of({ type: '[USER] Load User Fail', payload:[] }))
        )}) 

    )

}