import { Component, Output, EventEmitter, ViewChild, OnInit } from '@angular/core';
import { HeaderComponent } from './extra/header/header.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  @ViewChild(HeaderComponent, { static: false })
  private headerComponent: HeaderComponent
  title = 'infoTask';
  buttonValue: string;
  @Output() color: String;
  setColor: String = 'green';
  parentEvent(event) {
    this.buttonValue = event;
  }

  ngOnInit() {
    this.webWorker()
  }

  childFn(val) {
    this.buttonValue = val;
    this.setColor = val;
    this.headerComponent.passFn(val);
  }


  webWorker() {
    if (typeof Worker != 'undefined') {
      const _worker = new Worker('./web-worker/worker.worker.ts');
      _worker.onmessage = ((data) => {
        console.log('================================================================================>Worker giving message :' + data);
      });
      _worker.postMessage('Hello');
    } else {
      console.log('Your Browser not support web worker');
    }
  }





}
