import { Action } from "@ngrx/store";
import { userActionTypes } from '../shared/enum/todo-action';
import { Injectable } from "@angular/core";
import { HttpClient } from '@angular/common/http';





export const LOAD_USER = '[USER] Load User';
export const LOAD_USER_SUCCESS = '[USER] Load User Success';
export const LOAD_USER_FAIL = '[USER] Load User Fail';
export const ADD_USER_SUCCESS = '[USER] Add User';
export const ADD_USER_FAIL = '[USER] Add User fail';
export const REMOVE_USER = '[USER] Delete User';
export const REMOVE_USER_SUCCESS = '[USER] Delete User Success';
export const REMOVE_USER_FAIL = '[USER] Delete User Fail';
export const LIST_USER = '[USER] List';


// @Effect() userLIstEffect$ =  this.Action.ofType()



export class LoadUser implements Action{
    readonly type = LOAD_USER;
    constructor(public payload:userActionTypes){}
}
export class LoadUserSuccess implements Action{
    readonly type = LOAD_USER_SUCCESS;   
}
export class LoadUserFail implements Action{
    readonly type = LOAD_USER_FAIL;    
}
export class UserList implements Action{
    readonly type = LIST_USER;
    constructor(public payload:userActionTypes){}
}

export class AddUserSuccess implements Action{
    readonly type = ADD_USER_SUCCESS;
    constructor(public payload:userActionTypes){}
}
export class AddUserFail implements Action{
    readonly type = ADD_USER_FAIL;
    constructor(public payload:userActionTypes){}
}


export class DeleteUser implements Action{
    readonly type = REMOVE_USER;
    constructor(public payload:Number){}
}
export class DeleteUserSuccess implements Action{
    readonly type = REMOVE_USER_SUCCESS;
    constructor(public payload:Number){}
}
export class DeleteUserFail implements Action{
    readonly type = REMOVE_USER_FAIL;
    constructor(public payload:Number){}
}

export type UserActions = LoadUser | LoadUserSuccess | LoadUserFail;
export type RemoveUser = DeleteUser | DeleteUserSuccess | DeleteUserFail;
export type AddUser = AddUserSuccess | AddUserFail;