import { Directive } from '@angular/core';
import { NG_VALIDATORS, AbstractControl } from '@angular/forms';

let exitsMail=['shri@gmail.com','admin@gmail.com', 'abc@gmail.com']
@Directive({
    selector:'NameValidators',
})

export class NameValidatorsDirective {

}

export  function nameValidate(conttrol:AbstractControl){
    let count =0;
    exitsMail.map(e=>{
        
        if(e==conttrol.value){
            count ++;
        }
        // console.log(e, count);
    });

    if(count === 1){
        return {nameValid: true};
    }
    return null;
   
}