import { Directive, ElementRef, Renderer2} from '@angular/core';

@Directive({
  selector: '[appLogoHeader]'
})
export class LogoHeaderDirective {

  logoText:any;

  constructor(private el:ElementRef, private rnd:Renderer2) {
   
    this.logoText = this.rnd.createElement('span');
    let text = this.rnd.createText('App');
    this.rnd.appendChild(this.el.nativeElement, this.rnd.createText('Info'));
    this.rnd.appendChild(this.logoText, text);
    this.rnd.setAttribute(this.logoText, 'class','logo-app');
    this.rnd.setStyle(this.logoText, 'background-color','#fff');
    this.rnd.setStyle(this.logoText, 'border-radius','12px');
    this.rnd.setStyle(this.logoText, 'padding','4px');
    this.rnd.appendChild(this.el.nativeElement, this.logoText);
   
    // console.log('----------------------------------------------------------------///////////////------>', );
    // this.el.nativeElement.innerHTML = 'red'
   }
}
