import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { nameValidate } from "../auth.directive";
import { Router } from '@angular/router';



@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
loginForm:FormGroup;



  constructor(private fb:FormBuilder, private _router:Router) {
    this.loginForm = this.fb.group({
      email:['',[ Validators.required, Validators.email, nameValidate]],
      password:['',[ Validators.required, Validators.minLength(6)]],
      rememberme:[false]
    });
   }
  ngOnInit() {
  }
  get lg(){ return this.loginForm.controls }

  onSubmit(lgForm){
    if(lgForm.valid){
     if(!this.loginForm.controls['rememberme'].value) sessionStorage.setItem('isLogin','true');
     else localStorage.setItem('isLogin','true');
      this._router.navigate(["user",'shrikant3']);
    }else{
      return false;
    }
    
  }

}
