import { Injectable } from '@angular/core';


@Injectable({
  providedIn: 'root'
})
export class AuthService {
isLogin:boolean = false;
  constructor() {
       if(localStorage.getItem('isLogin') || sessionStorage.getItem('isLogin')) this.isLogin = true;
       else this.isLogin = false;
   }
   islogin(){
     if(this.islogin) return true
     else return false;
       
   }
}
